<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="store-list container-fluid">
    <div class="page-header mb-0 mt-0">
      <h4>原型规格列表</h4>
      <breadcrumb
        v-bind:items="[{text: '首页', link: '/'},{text: '商品管理', link: '/index/role/list'},{text: '原型列表', active: true}]"/>
    </div>
    <div class="panel panel-white">
      <div class="panel-body">
        <form id="search-form" class="j_search" method="get">
          <div class="form-inline form-group">
            <div class="form-group" style="height: 34px;">
              <input type="text" class="form-control" name="name" placeholder="关键词" v-bind:value="form.name"/>
            </div>
            <button class="btn btn-primary" type="submit" >查询</button>
            <a class="btn btn-default j_reset" >重置</a>
            <input type="hidden" class="form-control" name="prototypeId" v-bind:value="form.prototypeId"/>

            <router-link v-bind:to="'/goods/prototype/specification/create/'+form.prototypeId" class="btn btn-primary pull-right">添加规格</router-link>
          </div>
        </form>
        <table class="table table-bordered table-hover">
          <thead>
          <tr>
            <th>序号</th>
            <th>规格名称</th>
            <th>英文名称</th>
            <th>规格说明</th>
          <!--  <th>是否颜色</th>-->
            <th>排序</th>
            <th>操作</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="item in dataList">
            <td>{{item.id}}</td>
            <td>{{item.name}}</td>
            <td>{{item.enName}}</td>
            <td>{{item.description}}</td>
           <!-- <td>{{item.isColor?"是":"否"}}</td>-->
            <td>{{item.rank}}</td>
            <td>
              <router-link v-bind:to="'/goods/prototype/specification/option/list/'+ item.id">规格选项</router-link>
              <router-link v-bind:to="'/goods/prototype/specification/update/'+ item.id">编辑</router-link>
              <a href="#" v-on:click.prevent="remove(item.id)" >删除</a>
            </td>
          </tr>
          </tbody>
        </table>

        <pagination size="md"
                    :total-rows="pagination.totalRows"
                    :per-page="pagination.perPage"
                    v-model="currentPage">
        </pagination>

      </div> <!--panel-body end-->
    </div><!--panel end-->
  </div>
</template>


<script>
  import 'jquery-validation'
  import pagination from "@/components/bootstrap/pagination"
  import breadcrumb from "@/components/bootstrap/breadcrumb"

  export default{
    data: function () {
      return {
        resource:commonApp.api.prototype.specification,
        dataList: [],
        pagination: {
          totalRows: 0,
          perPage: 1
        },
        currentPage: 1,
        form: {
          name: '',
          prototypeId:this.$route.params.id
        }
      }
    },
    components: {
      pagination: pagination,
      breadcrumb: breadcrumb
    },
    created: function () {
      this.list();
    },
    mounted: function () {
      this.search();
      this.reset();
    },
    methods: {
      list: function () {
        var self = this;
        self.ajax({
          url: self.resource.list.url,
          data: self.form,
          success: function (res) {
            self.dataList = res.list;
            self.pagination.totalRows = res.totalRecord;
            self.pagination.perPage = res.pageSize;
            self.currentPage = res.pageIndex;
          }
        });
      },
      search: function () {
        var self = this;
        $('.j_search').submit(function (e) {
          e.preventDefault();
          self.form = commonApp.tools.getFromData($(this));
          self.list();
        });
      },
      reset: function () {
        var self = this;
        $('.j_reset').click(function (e) {
          e.preventDefault();
          self.form = {
            name: ''
          };
        });
      },
      remove: function (id) {
        var self = this;
        layer.confirm("确定删除该吗？", function () {
          self.ajax({
            type: 'POST',
            url: self.resource.delete.url+"?id="+id,
            success: function (res) {
              layer.msg(res.message, {time: 1000}, function () {
                if (res.code === 200) {
                  self.list();
                }
              });
            }
          });
        });
      }
  },
    watch: {
      currentPage: function (newData) {
        var self = this;
        if (self.$route.query.page != newData) {
          self.form.pageIndex = newData;
          self.list();
        }
      }
    }
  }
</script>



