<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="edit-wrap container-fluid">
    <div class="page-header mt-0 mb-0">
      <h4>订单发货</h4>
      <breadcrumb v-bind:items="[{text: '首页', link: '/'},{text: '订单管理', link: '/order/list'},{text: '订单发货', active: true}]"/>
    </div>
    <div class="panel panel-white">
      <div class="panel-body">
        <form id="myForm" method="post">
          <input type="hidden" name="orderId" v-bind:value="model.id"/>
          <div class="form-horizontal">
            <!--标题-->
            <div class="form-group">
              <label class="col-md-2">订单编号：</label>
              <div class="col-md-6">
                <input name=""  type="text" readonly="readonly" class="form-control" v-bind:value="model.orderNo"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">收货人：</label>
              <div class="col-md-6">
                <input name=""  type="text" readonly="readonly" class="form-control" v-bind:value="model.shipName"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">收货人地址：</label>
              <div class="col-md-6">
                <input name=""  type="text" readonly="readonly" class="form-control" v-bind:value="model.shipAddress"></div>
              </div>

            <div class="form-group">
              <label class="col-md-12">商品明细：</label>
              <div class="col-md-3" v-for="(item,index) in model.orderItemDomainList">
                <h6 class="col-md-12">商品{{index+1}}:</h6>
                <div class="col-md-12">
                <img class="col-md-4" src="https://timgsa.baidu.com/timg?image&quality=80&size=b10000_10000&sec=1494143493&di=f1cc326f18854debde6fbcdd08fa0124&src=http://vogue.moonbasa.com/files/2011/8/31/39-040180391862.jpg">
                <div class="col-md-8">
                  <div class="col-md-12">商品名：{{item.goodsName}}</div>
                  <div class="col-md-12">商品编号：{{item.goodsCode}}</div>
                  <div class="col-md-12">数量：{{item.num}}</div>
                  <div class="col-md-12">价格：{{item.goodsPrice}}</div>
                </div>
                </div>
              </div>
            </div>

            <div class="form-group">
              <label class="col-md-2">订单总价：</label>
              <div class="col-md-6">
                <input name=""  type="text" readonly="readonly" class="form-control" v-bind:value="model.orderTotal"></div>
            </div>

            <div class="form-group">
              <label class="col-md-2">填写快递公司：</label>
              <div class="col-md-6">
                <input name="shipperCompany"  type="text" class="form-control" v-bind:value="model.shipperCompany">
              </div>
            </div>
            <div class="form-group">
              <label class="col-md-2">填写快递单号：</label>
              <div class="col-md-6">
                <input name="trackingNumber" type="text" class="form-control" v-bind:value="model.trackingNumber"></div>
            </div>
            <!--发布-->
            <div class="form-group">
              <div class="col-md-3">
                <a class="btn btn-primary"  v-on:click.prevent="submitForm()">发货</a>
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
  import 'jquery-validation'
  import breadcrumb from "@/components/bootstrap/breadcrumb"

  export default{
    data: function () {
      return {
        model: {

        },
      }
    },
    components: {
      breadcrumb: breadcrumb
    },
    mounted: function () {
      this.validForm();
      this.check();
    },
    created: function () {
      this.getOrder();
    },
    updated: function () {

    },
    methods: {
      check:function () {
        var selects = document.getElementsByName("isValid");
        for (var i=0; i<selects.length; i++){
          if (selects[i].value=="1") {
            selects[i].checked= true;
            break;
          }
        }
      },
      getOrder: function () {
        var self = this;
        self.ajax({
          type: "GET",
          url: commonApp.api.order.get.url+"?id="+this.$route.params.id,
          success: function (res) {
            self.model = res;
          }
        });
      },
      validForm: function () {
        $('#myForm').validate({
          rules: {
            shipperCompany: {
              required: true
            },
            trackingNumber:{
              required: true
            }
          }
        });
      },
      submitForm: function () {
        var $form = $('#myForm');
        var self = this;
        var data = $form.serializeArray();
         if((data[1].value)==''||(data[2].value)=='') {
           layer.confirm("快递单号个快递公司必填！");
           return false;
         }
        layer.confirm("确定发货吗？", function () {
          self.ajax({
            type: 'POST',
            url: commonApp.api.order.sendGoods.url,
            data: $form.serialize(),
            success: function (res) {
              layer.msg(res.message, {time: 1000}, function () {
                if (res.code === 200) {
                  self.$router.push('/order/list');
                }
              });
            }
          })
        });
      }
    }
  }
</script>



