<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="edit-wrap container-fluid">
    <div class="page-header mt-0 mb-0">
      <h4>预约单明细</h4>
      <breadcrumb v-bind:items="[{text: '首页', link: '/'},{text: '预约单管理', link: '/reservation/list'},{text: '预约单明细', active: true}]"/>
    </div>
    <div class="panel panel-white">
      <div class="panel-body">
        <form id="myForm" method="post">
          <input type="hidden" name="orderId" v-bind:value="model.id"/>
          <div class="form-horizontal">
            <!--标题-->
            <div class="form-group">
              <label class="col-md-2">预约单编号：</label>
              <div class="col-md-6">
                <input name=""  type="text" readonly="readonly" class="form-control" v-bind:value="model.reservationNo"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">预约人：</label>
              <div class="col-md-6">
                <input name=""  type="text" readonly="readonly" class="form-control" v-bind:value="model.customerDomain.lastName+model.customerDomain.firstName"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">预约人地址：</label>
              <div class="col-md-6">
                <input name=""  type="text" readonly="readonly" class="form-control" v-bind:value="model.address"></div>
            </div>

            <div class="form-group">
              <label class="col-md-12">商品明细：</label>
              <div class="col-md-3" v-for="(item,index) in model.reservationItemDomainList">
                <h6 class="col-md-12">商品{{index+1}}:</h6>
                <div class="col-md-12">
                <img class="col-md-4" src="https://timgsa.baidu.com/timg?image&quality=80&size=b10000_10000&sec=1494143493&di=f1cc326f18854debde6fbcdd08fa0124&src=http://vogue.moonbasa.com/files/2011/8/31/39-040180391862.jpg">
                <div class="col-md-8">
                  <div class="col-md-12">商品名：{{item.goodsName}}</div>
                  <div class="col-md-12">商品编号：{{item.goodsCode}}</div>
                  <div class="col-md-12">数量：{{item.num}}</div>
                  <div class="col-md-12">颜色：</div>
                  <div class="col-md-12">型号：</div>
                </div>
                </div>
              </div>
            </div>

            <div class="form-group">
              <label class="col-md-2">预约分店：</label>
              <div class="col-md-6">
                <input name=""  type="text" readonly="readonly" class="form-control" v-bind:value="model.storeTitle "></div>
            </div>
            <!--发布-->
            <div class="form-group">
              <div class="col-md-3">
                <router-link  v-bind:to="'/reservation/list'" class="btn btn-primary">返回</router-link>
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
  import 'jquery-validation'
  import breadcrumb from "@/components/bootstrap/breadcrumb"

  export default{
    data: function () {
      return {
        model: {

        },
      }
    },
    components: {
      breadcrumb: breadcrumb
    },
    mounted: function () {
    },
    created: function () {
      this.getOrder();
    },
    updated: function () {

    },
    methods: {
      getOrder: function () {
        var self = this;
        self.ajax({
          type: "GET",
          url: commonApp.api.reservation.get.url+"?id="+this.$route.params.id,
          success: function (res) {
            self.model = res;
          }
        });
      }
    }
  }
</script>



