<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="store-list container-fluid">
    <div class="page-header mb-0 mt-0">
      <h4>原型列表</h4>
      <breadcrumb
        v-bind:items="[{text: '首页', link: '/'},{text: '订单管理', link: '/index/role/list'},{text: '原型列表', active: true}]"/>
    </div>
    <div class="panel panel-white">
      <div class="panel-body">
        <form id="search-form" class="j_search" method="get">
          <div class="form-inline form-group">
            <div class="form-group">
              <input type="text" class="form-control" name="name" placeholder="关键词" v-bind:value="form.name"/>
            </div>
            <button class="btn btn-primary" type="submit" >查询</button>
            <a class="btn btn-default j_reset" >重置</a>
            <router-link to="/order/create" class="btn btn-primary pull-right">添加订单</router-link>
          </div>
        </form>
        <table class="table table-bordered table-hover">
          <thead>
          <tr>
            <th>序号</th>
            <th>订单编号</th>
            <th>客户Id</th>
            <th>订单状态</th>
            <th>下单时间</th>
            <th>配送方式</th>
            <th>发货时间</th>
            <th>快递公司</th>
            <th>快递单号</th>
            <th>取货门店Id</th>
            <th>收货人姓名</th>
            <th>收货人电话</th>
            <th>收货人地址</th>
            <th>收货人备注</th>
            <th>支付方式</th>
            <th>付款时间</th>
            <th>优惠金额</th>
            <th>运费</th>
            <th>订单应收金额</th>
            <th>操作</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="item in dataList">
            <td>{{item.id}}</td>
            <td>{{item.orderNo}}</td>
            <td>{{item.customerId}}</td>
            <td>{{item.status}}</td>
            <td>{{item.orderTime | formatDate}}</td>
            <td>{{item.shippingMethod}}</td>
            <td>{{item.shippedTime | formatDate}}</td>
            <td>{{item.shippedCompany}}</td>
            <td>{{item.trackingNumber}}</td>
            <td>{{item.shipShopId}}</td>
            <td>{{item.shipName}}</td>
            <td>{{item.shipPhone}}</td>
            <td>{{item.shipAddress}}</td>
            <td>{{item.shipMemo}}</td>
            <td>{{item.paymentMethod}}</td>
            <td>{{item.paidTime}}</td>
            <td>{{item.couponDiscount}}</td>
            <td>{{item.shipFee}}</td>
            <td>{{item.orderTotal}}</td>
            <td>
              <router-link v-bind:to="'/order/update/'+ item.id">订单详情</router-link>
              <a href="#" v-on:click.prevent="remove(item.id)">删除</a>
            </td>
          </tr>
          </tbody>
        </table>

        <pagination size="md"
                    :total-rows="pagination.totalRows"
                    :per-page="pagination.perPage"
                    v-model="currentPage">
        </pagination>

      </div> <!--panel-body end-->
    </div><!--panel end-->
  </div>
</template>


<script>
  import 'jquery-validation'
  import pagination from "@/components/bootstrap/pagination"
  import breadcrumb from "@/components/bootstrap/breadcrumb"

  export default{
    data: function () {
      return {
        resource:commonApp.api.order,
        dataList: [],
        pagination: {
          totalRows: 0,
          perPage: 1
        },
        currentPage: 1,
        form: {
          name: ''
        }
      }
    },
    components: {
      pagination: pagination,
      breadcrumb: breadcrumb
    },
    created: function () {
      this.list();
    },
    mounted: function () {
      this.search();
      this.reset();
    },
    methods: {
      list: function () {
        var self = this;
        self.ajax({
          url: self.resource.list.url,
          data: self.form,
          success: function (res) {
            self.dataList = res.list;
            self.pagination.totalRows = res.totalRecord;
            self.pagination.perPage = res.pageSize;
            self.currentPage = res.pageIndex;
          }
        });
      },
      search: function () {
        var self = this;
        $('.j_search').submit(function (e) {
          e.preventDefault();
          self.form = commonApp.tools.getFromData($(this));
          self.list();
        });
      },
      reset: function () {
        var self = this;
        $('.j_reset').click(function (e) {
          e.preventDefault();
          self.form = {
            name: ''
          };
        });
      },
      remove: function (id) {
        var self = this;
        layer.confirm("确定删除吗？", function () {
          self.ajax({
            type: 'POST',
            url: self.resource.delete.url+"?id="+id,
            success: function (res) {
              layer.msg(res.message, {time: 1000}, function () {
                if (res.code === 200) {
                  self.list();
                }
              });
            }
          });
        });
      }
  },
    watch: {
      currentPage: function (newData) {
        var self = this;
        if (self.$route.query.page != newData) {
          self.form.pageIndex = newData;
          self.list();
        }
      }
    }
  }
</script>



