<template xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="edit-wrap container-fluid">
    <div class="page-header mt-0 mb-0">
      <h4>会员详情</h4>
      <breadcrumb v-bind:items="[{text: '首页', link: '/'},{text: '会员管理', link: '/customer/list'},{text: '会员编辑', active: true}]"/>
    </div>
    <div class="panel panel-white">
      <div class="panel-body">
        <form id="myForm" method="post">
          <input type="hidden" name="id" v-bind:value="model.id"/>
          <div class="form-horizontal">
            <!--标题-->
            <div class="form-group">
              <label class="col-md-2">姓：</label>
              <div class="col-md-6">
                <input name="lastName" readonly="readonly" type="text" class="form-control" v-bind:value="model.lastName"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">名：</label>
              <div class="col-md-6">
                <input name="firstName" readonly="readonly" type="text" class="form-control" v-bind:value="model.firstName"></div>
            </div>

            <div class="form-group">
              <label class="col-md-2">邮件：</label>
              <div class="col-md-6">
                <input name="email" readonly="readonly" type="email" class="form-control" v-bind:value="model.email"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">电话：</label>
              <div class="col-md-6">
                <input name="phone" readonly="readonly" type="text" class="form-control" v-bind:value="model.phone"></div>
            </div>

            <div class="form-group">
              <label class="col-md-2">登录帐号：</label>
              <div class="col-md-6">
                <input name="" readonly="readonly" type="text" class="form-control" v-bind:value="model.accountDomain.userName">
              </div>
            </div>

            <div class="form-group">
              <label class="col-md-2">订阅类型：</label>
              <div class="col-md-6">
                <input name="subscribeType" readonly="readonly" type="text" class="form-control" v-bind:value="model.subscribeType"></div>
            </div>

            <div class="form-group">
              <label class="col-md-2">是否club会员：</label>
              <div class="col-md-6">
                <input type="radio" disabled="disabled" name="isArtClubMember"  value="1"  v-model="model.isArtClubMember"/>是
                <input type="radio" disabled="disabled" name="isArtClubMember"   value="0" v-model="model.isArtClubMember" />否</div>
            </div>

            <div class="form-group">
              <label class="col-md-2">是否有效：</label>
              <div class="col-md-6">
                <input type="radio" disabled="disabled" name="model.accountDomain.isValid"  value="1"  v-model="model.accountDomain.isValid"/>是
                <input type="radio" disabled="disabled" name="model.accountDomain.isValid"   value="0" v-model="model.accountDomain.isValid" />否</div>
            </div>

            <div class="form-group">
              <label class="col-md-2">会员等级：</label>
              <div class="col-md-6">
                <input name="customerLevel" readonly="readonly" type="text" class="form-control" v-bind:value="model.customerLevel"></div>
            </div>

            <div class="form-group" v-for="(item,index) in model.customerAddressList" >
              <label class="col-md-2">地址{{index+1}}：</label>
              <div class="col-md-6">
                <textarea type="text" readonly="readonly" class="form-control" name="" v-bind:value="item.address" ></textarea>
              </div>
            </div>


            <!--发布-->
             <div class="form-group">
              <div class="col-md-3">
                <router-link  v-bind:to="'/customer/list'" class="btn btn-primary">返回</router-link>
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
  import 'jquery-validation'
  import breadcrumb from "@/components/bootstrap/breadcrumb"

  export default{
    data: function () {
      return {
        resource:commonApp.api.customer,
        model: {

        },
      }
    },
    components: {
      breadcrumb: breadcrumb
    },
    mounted: function () {
    },
    created: function () {
      this.get();
    },
    updated: function () {
    },
    methods: {
      get: function () {
        var self = this;
        self.ajax({
          type: "GET",
          url: commonApp.api.customer.get.url+"?id="+this.$route.params.id,
          success: function (res) {
            self.model = res;
          }
        });
      }

    }
  }
</script>



