<template>
    <ol class="breadcrumb">
        <li v-for="item in items" :class="['breadcrumb-item', item.active ? 'active' : null]">
          <span v-if="item.active" v-html="item.text"></span>
          <router-link v-else :to="item.to||item.href||item.link" v-html="item.text"></router-link>
        </li>
      <slot></slot>
    </ol>
</template>

<script>
    export default {
        computed: {},
        props: {
            items: {
                type: Array,
                default: () => [],
                required: true
            }
        },
        methods: {
            onclick(item) {
                this.$emit('click', item);
            }
        }
    };
</script>



