<template xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="edit-wrap container-fluid">
    <div class="page-header mt-0 mb-0">
      <h4>商品创建</h4>
      <breadcrumb v-bind:items="[{text: '首页', link: '/'},{text: '商品管理', link: '/goods/list'},{text: '商品创建', active: true}]"/>
    </div>
    <div class="panel panel-white">
      <div class="panel-body">
        <form id="myForm" method="post">
          <input type="hidden" name="id" v-bind:value="model.id"/>
          <div class="form-horizontal">
            <!--标题-->
            <div class="form-group">
              <label class="col-md-2">商品名称：</label>
              <div class="col-md-6">
                <input name="name"  type="text" class="form-control" v-bind:value="model.name"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">商品编号：</label>
              <div class="col-md-6">
                <input name="code"  type="text" class="form-control" v-bind:value="model.code"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">分类：</label>
              <div class="col-md-6">
                <select name="categoryId" class="form-control" v-model="model.categoryId">
                  <optgroup v-for="item in categoryList"  v-bind:label="item.name">
                    <option v-for="child in item.children" v-bind:value="child.id">{{child.name}}</option>
                  </optgroup>

                </select>
              </div>
            </div>
            <div class="form-group">
              <label class="col-md-2">缩略图</label>
              <div class="col-md-6">
                <div class="list-img list-img-multiple" id="upload_image">
                  <input class="upload_input" name="thumb" type="hidden" v-bind:value="model.thumb"/>
                  <ul class="upload_list"></ul>
                  <a class="upload upload_btn" href="javascript:;"><span class="ion ion-plus"></span></a>
                </div>
              </div>
            </div>
            <div class="form-group">
              <label class="col-md-2">描述：</label>
              <div class="col-md-6">
                <textarea type="text"  class="form-control" name="description" v-bind:value="model.description" ></textarea>
              </div>
            </div>
            <div class="form-group">
              <label class="col-md-2">商品详情：</label>
              <div class="col-md-6 clearfix">
                <textarea type="text"  name="details" id="js_details" v-bind:value="model.details" ></textarea>
              </div>
            </div>
            <div class="form-group">
              <label class="col-md-2">状态：</label>
              <div class="col-md-6">
                <input type="radio"  name="isPublished"  value="1"  v-model="model.isPublished"/>上架
                <input type="radio"  name="isPublished"   value="0" v-model="model.isPublished" />下架</div>
            </div>
            <!--发布-->
            <div class="form-group">
              <div class="col-md-3">
                <button class="btn btn-primary" type="submit">保存</button>
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
  import 'jquery-validation'
  import '../../../static/js/plugins/ueditor/ueditor.config'
  import '../../../static/js/plugins/ueditor/ueditor.all.min'
  import '../../../static/js/plugins/uploadUeditor'
  import breadcrumb from "@/components/bootstrap/breadcrumb"

  export default{
    data: function () {
      return {
        resource:commonApp.api.goods,
        model: {
          isPublished:1
        },
        categoryList:[]
      }
    },
    components: {
      breadcrumb: breadcrumb
    },
    mounted: function () {
      this.submitForm();
      this.upload();
      UE.getEditor('js_details',{
        initialFrameHeight:300
      });
    },
    created: function () {
      this.upload();
      this.ListCategory();
      UE.getEditor('js_details',{
        initialFrameHeight:300
      });
    },
    updated: function () {
    },
    methods: {
      ListCategory:function () {
        var self = this;
        return  self.ajax({
          type: "GET",
          url: commonApp.api.goods.category.list.url+"?level=1",
          success: function (res) {
            self.categoryList = res;
          }
        });
      },
      submitForm: function () {
        var self = this;
        $('#myForm').validate({
          rules: {
            name: {
              required: true
            },
            description:{
              required: true
            }
          },
          submitHandler: function (form) {
            var $form = $(form);
            self.ajax({
              type: "POST",
              url: self.resource.create.url,
              data: $form.serialize(),
              success: function (res) {
                layer.msg(res.message, {time: 1000}, function () {
                  if (res.code === 200) {
                    self.$router.push('/goods/list');
                  }
                });
              }
            });
          }
        });
      },
      upload: function () {
        uploadUeditor.singleImage($('#upload_image'));
      }
    }
  }
</script>



