<template xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="edit-wrap container-fluid">
    <div class="page-header mt-0 mb-0">
      <h4>原型创建</h4>
      <breadcrumb v-bind:items="[{text: '首页', link: '/'},{text: '角色管理', link: '/index/role/list'},{text: '角色编辑', active: true}]"/>
    </div>
    <div class="panel panel-white">
      <div class="panel-body">
        <form id="myForm" method="post">
          <input type="hidden" name="id" v-bind:value="model.id"/>
          <input type="hidden" name="specificationId" v-bind:value="model.specificationId"/>

          <div class="form-horizontal">
            <!--标题-->
            <div class="form-group">
              <label class="col-md-2">选项名称：</label>
              <div class="col-md-6">
                <input name="name"  type="text" class="form-control" v-bind:value="model.name"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">英文名称：</label>
              <div class="col-md-6">
                <input name="enName"  type="text" class="form-control" v-bind:value="model.enName"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">排序：</label>
              <div class="col-md-6">
                <input name="rank"  type="text" class="form-control" v-bind:value="model.rank"></div>
            </div>
            <!--发布-->
            <div class="form-group">
              <div class="col-md-3">
                <button class="btn btn-primary" type="submit">保存</button>
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
  import 'jquery-validation'
  import breadcrumb from "@/components/bootstrap/breadcrumb"

  export default{
    data: function () {
      return {
        resource:commonApp.api.prototype.specification.option,
        model: {
          isColor:1,
          specificationId:this.$route.params.id,
          rank:99
        },
      }
    },
    components: {
      breadcrumb: breadcrumb
    },
    mounted: function () {
      this.submitForm();
    },
    created: function () {
      this.get();
    },
    updated: function () {

    },
    methods: {
      get: function () {
        var self = this;
        self.ajax({
          type: "GET",
          url: self.resource.get.url+"?id="+this.$route.params.id,
          success: function (res) {
            self.model = res;
          }
        });
      },
      submitForm: function () {
        var self = this;
        $('#myForm').validate({
          rules: {
            name: {
              required: true
            }
          },
          submitHandler: function (form) {
            var $form = $(form);
            self.ajax({
              type: "POST",
              url: self.resource.update.url,
              data: $form.serialize(),
              success: function (res) {
                layer.msg(res.message, {time: 1000}, function () {
                  if (res.code === 200) {
                    window.history.go(-1)
                  }
                });
              }
            });
          }
        });
      }
    }
  }
</script>



