import Vue from 'vue'
import Router from 'vue-router'
import index from '@/views/index'
import login from '@/views/passport/login'
import dashboard from '@/views/dashboard'
import goodsList from '@/views/goods/list'
import goodsCreate from '@/views/goods/create'
import goodsUpdate from '@/views/goods/update'
import goodsColorList from '@/views/goods/color/list'
import goodsColorCreate from '@/views/goods/color/create'
import goodsColorUpdate from '@/views/goods/color/update'
import goodsItemList from '@/views/goods/item/list'
import goodsItemCreate from '@/views/goods/item/create'
import goodsItemUpdate from '@/views/goods/item/update'
import goodsSkuList from '@/views/goods/sku/list'
import goodsSkuCreate from '@/views/goods/sku/create'
import goodsSkuUpdate from '@/views/goods/sku/update'
import goodsCategoryList from '@/views/goods/category/list'
import goodsCategoryCreate from '@/views/goods/category/create'
import goodsCategoryUpdate from '@/views/goods/category/update'
import prototypeList from '@/views/goods/prototype/list'
import prototypeCreate from '@/views/goods/prototype/create'
import prototypeUpdate from '@/views/goods/prototype/update'
import prototypeSpecificationList from '@/views/goods/prototype/specification/List'
import prototypeSpecificationCreate from '@/views/goods/prototype/specification/create'
import prototypeSpecificationUpdate from '@/views/goods/prototype/specification/update'
import prototypeSpecificationOptionList from '@/views/goods/prototype/specification/option/List'
import prototypeSpecificationOptionCreate from '@/views/goods/prototype/specification/option/create'
import prototypeSpecificationOptionUpdate from '@/views/goods/prototype/specification/option/update'
import orderList from '@/views/order/list'
import orderCreate from '@/views/order/create'
import orderSendGoods from '@/views/order/sendGoods'
import orderDetail from '@/views/order/detail'

import customerList from '@/views/customer/list'
import customerDetail from '@/views/customer/detail'

import accountList from '@/views/account/list'
import accountCreate from '@/views/account/create'
import accountUpdate from '@/views/account/update'

import returnRequestList from '@/views/returnRequest/list'
import returnRequestCreate from '@/views/returnRequest/create'
import returnRequestUpdate from '@/views/returnRequest/detail'

import reservationList from '@/views/reservation/list'
import reservationCreate from '@/views/reservation/create'
import reservationUpdate from '@/views/reservation/detail'

import storeList from '@/views/store/list'
import storeCreate from '@/views/store/create'
import storeUpdate from '@/views/store/update'
import storeCityList from '@/views/store/city/list'
import storeCityCreate from '@/views/store/city/create'
import storeCityUpdate from '@/views/store/city/update'
import storeCountryList from '@/views/store/country/list'
import storeCountryCreate from '@/views/store/country/create'
import storeCountryUpdate from '@/views/store/country/update'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/dashboard',
    },
    {
      path: '/passport/login',
      name: 'login',
      component: login
    },
    {
      path: '/',
      component: index,
      children: [
        {path: 'dashboard', component: dashboard},
        /*商品*/
        {path: 'goods/list', component: goodsList},
        {path: 'goods/create', component: goodsCreate},
        {path: 'goods/update/:id', component: goodsUpdate},
        /*商品颜色*/
        {path: 'goods/color/list', component: goodsColorList},
        {path: 'goods/color/create', component: goodsColorCreate},
        {path: 'goods/color/update/:id', component: goodsColorUpdate},
        /*商品项目*/
        {path: 'goods/item/list/:id', component: goodsItemList},
        {path: 'goods/item/create/:id', component: goodsItemCreate},
        {path: 'goods/item/update/:id', component: goodsItemUpdate},
        /*商品sku*/
        {path: 'goods/sku/list/:id', component: goodsSkuList},
        {path: 'goods/sku/create/:id', component: goodsSkuCreate},
        {path: 'goods/sku/update/:id', component: goodsSkuUpdate},
        /*商品分类*/
        {path: 'goods/category/list', component: goodsCategoryList},
        {path: 'goods/category/create', component: goodsCategoryCreate},
        {path: 'goods/category/update/:id', component: goodsCategoryUpdate},
        /*商品原型*/
        {path: 'goods/prototype/list', component: prototypeList},
        {path: 'goods/prototype/create', component: prototypeCreate},
        {path: 'goods/prototype/update/:id', component: prototypeUpdate},
        /*商品规格*/
        {path: 'goods/prototype/specification/list/:id', component: prototypeSpecificationList},
        {path: 'goods/prototype/specification/create/:id', component: prototypeSpecificationCreate},
        {path: 'goods/prototype/specification/update/:id', component: prototypeSpecificationUpdate},
        /*规格选项*/
        {path: 'goods/prototype/specification/option/list/:id', component: prototypeSpecificationOptionList},
        {path: 'goods/prototype/specification/option/create/:id', component: prototypeSpecificationOptionCreate},
        {path: 'goods/prototype/specification/option/update/:id', component: prototypeSpecificationOptionUpdate},
        /*订单*/
        {path: 'order/list', component: orderList},
        {path: 'order/create', component: orderCreate},
        {path: 'order/sendGoods/:id', component: orderSendGoods},
        {path: 'order/get/:id', component: orderDetail},
        /*会员*/
        {path: 'customer/list', component: customerList},
        {path: 'customer/get/:id', component: customerDetail},
        /*用户*/
        {path: 'account/list', component: accountList},
        {path: 'account/create', component: accountCreate},
        {path: 'account/update/:id', component: accountUpdate},

        /*退货单*/
        {path: 'returnRequest/list', component: returnRequestList},
        {path: 'returnRequest/create', component: returnRequestCreate},
        {path: 'returnRequest/get/:id', component: returnRequestUpdate},
        /*预定单*/
        {path: 'reservation/list', component: reservationList},
        {path: 'reservation/create', component: reservationCreate},
        {path: 'reservation/get/:id', component: reservationUpdate},
        /*门店*/
        {path: 'store/list', component: storeList},
        {path: 'store/create', component: storeCreate},
        {path: 'store/update/:id', component: storeUpdate},
        /*门店国家*/
        {path: 'store/country/list', component: storeCountryList},
        {path: 'store/country/create', component: storeCountryCreate},
        {path: 'store/country/update/:id', component: storeCountryUpdate},
        /*门店城市*/
        {path: 'store/city/list', component: storeCityList},
        {path: 'store/city/create', component: storeCityCreate},
        {path: 'store/city/update/:id', component: storeCityUpdate},

      ]
    },
  ]
})



