<template xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="edit-wrap container-fluid">
    <div class="page-header mt-0 mb-0">
      <h4>原型创建</h4>
      <breadcrumb v-bind:items="[{text: '首页', link: '/'},{text: '原型管理', link: '/goods/prototype/list'},{text: '原型创建', active: true}]"/>
    </div>
    <div class="panel panel-white">
      <div class="panel-body">
        <form id="myForm" method="post">
          <input type="hidden" name="id" v-bind:value="prototype.id"/>
          <div class="form-horizontal">
            <!--标题-->
            <div class="form-group">
              <label class="col-md-2">原型名称：</label>
              <div class="col-md-6">
                <input name="name"  type="text" class="form-control" v-bind:value="prototype.name"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">原型编号：</label>
              <div class="col-md-6">
                <input name="code"  type="text" class="form-control" v-bind:value="prototype.code"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">状态：</label>
              <div class="col-md-6">
                <input type="radio"  name="isValid"  value="1"  v-model="prototype.isValid"/>有效
                <input type="radio"  name="isValid"   value="0" v-model="prototype.isValid" />无效</div>
            </div>
            <div class="form-group">
              <label class="col-md-2">描述：</label>
              <div class="col-md-6">
                <textarea type="text"  class="form-control" name="description" v-bind:value="prototype.description" ></textarea>
              </div>
            </div>

            <!--发布-->
            <div class="form-group">
              <div class="col-md-3">
                <button class="btn btn-primary" type="submit">保存</button>
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
  import 'jquery-validation'
  import breadcrumb from "@/components/bootstrap/breadcrumb"

  export default{
    data: function () {
      return {
        resource:commonApp.api.goods.prototype,
        prototype: {

        },
      }
    },
    components: {
      breadcrumb: breadcrumb
    },
    mounted: function () {
      this.submitForm();
      this.check();
    },
    created: function () {

    },
    updated: function () {

    },
    methods: {
      check:function () {
        var selects = document.getElementsByName("isValid");
        for (var i=0; i<selects.length; i++){
          if (selects[i].value=="1") {
            selects[i].checked= true;
            break;
          }
        }
      },
      submitForm: function () {
        var self = this;
        $('#myForm').validate({
          rules: {
            name: {
              required: true
            },
            description:{
              required: true
            }
          },
          submitHandler: function (form) {
            var $form = $(form);
            self.ajax({
              type: "POST",
              url: commonApp.api.goods.prototype.create.url,
              data: $form.serialize(),
              success: function (res) {
                layer.msg(res.message, {time: 1000}, function () {
                  if (res.code === 200) {
                    self.$router.push('/goods/prototype/list');
                  }
                });
              }
            });
          }
        });
      }
    }
  }
</script>



