<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="store-list container-fluid">
    <div class="page-header mb-0 mt-0">
      <h4>会员列表</h4>
      <breadcrumb
        v-bind:items="[{text: '首页', link: '/'},{text: '会员管理', link: '/customer/list'},{text: '会员列表', active: true}]"/>
    </div>
    <div class="panel panel-white">
      <div class="panel-body">
        <form id="search-form" class="j_search" method="get">
          <div class="form-inline form-group">
            <div class="form-group">
              <input type="text" class="form-control" name="firstName" placeholder="关键词" v-bind:value="form.firstName"/>
            </div>
            <button class="btn btn-primary" type="submit" >查询</button>
            <a class="btn btn-default j_reset" >重置</a>
          </div>
        </form>
        <table class="table table-bordered table-hover">
          <thead>
          <tr>
            <th>序号</th>
            <th>名</th>
            <th>姓</th>
            <th>邮件</th>
            <th>电话</th>
            <th>会员等级</th>
            <th>创建时间</th>
            <th>是否可用</th>
            <th>订阅类型</th>
            <th>操作</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="item in dataList">
            <td>{{item.id}}</td>
            <td>{{item.firstName}}</td>
            <td>{{item.lastName}}</td>
            <td>{{item.email}}</td>
            <td>{{item.phone}}</td>
            <td>{{item.customerLevel}}</td>
            <td>{{item.createTime | formatDate}}</td>
            <td>{{item.accountDomain.isValid==1?"可用":"不可用"}}</td>
            <td>{{item.subscribeType}}</td>
            <td>
              <router-link v-bind:to="'/customer/get/'+ item.id" class="btn btn-default">会员详情</router-link>
              <a href="#" v-on:click.prevent="forbid(item.id)" v-if="item.accountDomain.isValid==1"  class="btn btn-warning">禁用</a>
              <a href="#" v-on:click.prevent="enable(item.id)" v-if="item.accountDomain.isValid==0" class="btn btn-success">启用</a>
            </td>
          </tr>
          </tbody>
        </table>

        <pagination size="md"
                    :total-rows="pagination.totalRows"
                    :per-page="pagination.perPage"
                    v-model="currentPage">
        </pagination>

      </div> <!--panel-body end-->
    </div><!--panel end-->
  </div>
</template>


<script>
  import 'jquery-validation'
  import pagination from "@/components/bootstrap/pagination"
  import breadcrumb from "@/components/bootstrap/breadcrumb"

  export default{
    data: function () {
      return {
        resource:commonApp.api.customer,
        dataList: [],
        pagination: {
          totalRows: 0,
          perPage: 1
        },
        currentPage: 1,
        form: {
          firstName: ''
        }
      }
    },
    components: {
      pagination: pagination,
      breadcrumb: breadcrumb
    },
    created: function () {
      this.list();
    },
    mounted: function () {
      this.search();
      this.reset();
    },
    methods: {
      list: function () {
        var self = this;
        self.ajax({
          url: self.resource.list.url,
          data: self.form,
          success: function (res) {
            self.dataList = res.list;
            self.pagination.totalRows = res.totalRecord;
            self.pagination.perPage = res.pageSize;
            self.currentPage = res.pageIndex;
          }
        });
      },
      search: function () {
        var self = this;
        $('.j_search').submit(function (e) {
          e.preventDefault();
          self.form = commonApp.tools.getFromData($(this));
          self.list();
        });
      },
      reset: function () {
        var self = this;
        $('.j_reset').click(function (e) {
          e.preventDefault();
          self.form = {
            firstName: ''
          };
        });
      },
      forbid: function (id) {
        var self = this;
        layer.confirm("确定禁用吗？", function () {
          self.ajax({
            type: 'POST',
            url: self.resource.forbid.url+"?id="+id,
            success: function (res) {
              layer.msg(res.message, {time: 1000}, function () {
                if (res.code === 200) {
                  self.list();
                }
              });
            }
          });
        });
      },
      enable: function (id) {
        var self = this;
        layer.confirm("确定启用吗？", function () {
          self.ajax({
            type: 'POST',
            url: self.resource.enable.url+"?id="+id,
            success: function (res) {
              layer.msg(res.message, {time: 1000}, function () {
                if (res.code === 200) {
                  self.list();
                }
              });
            }
          });
        });
      }

  },
    watch: {
      currentPage: function (newData) {
        var self = this;
        if (self.$route.query.page != newData) {
          self.form.pageIndex = newData;
          self.list();
        }
      }
    }
  }
</script>



