import moment from 'moment'

Vue.config.productionTip = false

// 定义layerjs的皮肤
layer.config({
  path: '../static/js/plugins/layer/'
})
Vue.filter("parseImageSrc", function(value) {
  var result="";
  if(value){
    var image= JSON.parse(value)[0];
    result=image.file;
  }
  return result;
});
Vue.filter("parseImageList", function(value) {
  var result="";
  if(value){
    result= JSON.parse(value);
  }
  return result;
});
Vue.filter('formatDate',function (value,format) {
    if (!value) return ''
    if (!format) format = 'YYYY-MM-DD hh:mm:ss';
    value = parseInt(value);
    return moment(value).format(format);
});

Vue.directive('formatDate', {
  componentUpdated: function (el,binding) {
    console.log(binding.value)
    var format = 'YYYY-MM-DD hh:mm:ss';
    var date=moment(parseInt(binding.value)).format(format);
    $(el).val(date);
  }
});

function ajaxFunction(url, type, data, callback) {
 return $.ajax({
    url: url,
    type: type,
    data: data,
    success: callback,
    statusCode: {
      401: function () {
        router.push('/passport/login');
      }
    }
  });
}
Vue.prototype.ajax = function (obj) {
  obj.data = getData(obj.data);
  return ajaxFunction(obj.url, obj.type, obj.data, obj.success);
}
Vue.prototype.get = function (url, data, callback) {
  if (typeof data == "function") {
    callback = data;
  }
  data = getData(data);
  return ajaxFunction(url, "GET", data, callback);
}
Vue.prototype.getJSON = function (url, data, callback) {
  if (typeof data == "function") {
    callback = data;
  }
  data = getData(data);
  return  ajaxFunction(url, "GET", data, callback);
}
Vue.prototype.post = function (url, data, callback) {
  if (typeof data == "function") {
    callback = data;
  }
  data = getData(data);
  return ajaxFunction(url, "POST", data, callback);
}
