<template xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="edit-wrap container-fluid">
    <div class="page-header mt-0 mb-0">
      <h4>商品项目创建</h4>
      <breadcrumb v-bind:items="[{text: '首页', link: '/'},{text: '商品管理', link: '/goods/list'},{text: '商品项目', link: '/goods/item/list'},{text: '商品项目创建', active: true}]"/>
    </div>
    <div class="panel panel-white">
      <div class="panel-body">
        <form id="myForm" method="post">
          <input type="hidden" name="id" v-bind:value="model.id"/>
          <input type="hidden" name="goodsId" v-bind:value="model.goodsId"/>
          <div class="form-horizontal">
            <!--标题-->
            <div class="form-group">
              <label class="col-md-2">项目名称：</label>
              <div class="col-md-6">
                <input name="name"  type="text" class="form-control" v-bind:value="model.name"></div>
            </div>
            <!--标题-->
            <div class="form-group">
              <label class="col-md-2">英文名称：</label>
              <div class="col-md-6">
                <input name="enName"  type="text" class="form-control" v-bind:value="model.enName"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">排序：</label>
              <div class="col-md-6">
                <input name="rank"  type="text" class="form-control" v-bind:value="model.rank"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">颜色：</label>
              <div class="col-md-6">
                <select name="colorId" class="form-control" v-model="model.colorId">
                  <option v-for="item in colorList" v-bind:value="item.id">{{item.name}}</option>
                </select>
              </div>
            </div>
            <div class="form-group">
              <label class="col-md-2">价格：</label>
              <div class="col-md-6">
                <input name="price"  type="text" class="form-control" v-bind:value="model.price"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">折扣价：</label>
              <div class="col-md-6">
                <input name="discountPrice"  type="text" class="form-control" v-bind:value="model.discountPrice"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">缩略图</label>
              <div class="col-md-6">
                <div class="list-img list-img-multiple" id="upload_image">
                  <input class="upload_input" name="thumb" type="hidden" v-bind:value="model.thumb"/>
                  <ul class="upload_list"></ul>
                  <a class="upload upload_btn" href="javascript:;"><span class="ion ion-plus"></span></a>
                </div>
              </div>
            </div>
            <div class="form-group">
              <label class="col-md-2">相册照片</label>
              <div class="col-md-6">
                <div class="list-img list-img-multiple" id="upload_photos">
                  <input class="upload_input" name="photos" type="hidden" v-bind:value="model.photos"/>
                  <ul class="upload_list"></ul>
                  <a class="upload upload_btn" href="javascript:;"><span class="ion ion-plus"></span></a>
                </div>
              </div>
            </div>
            <div class="form-group">
              <label class="col-md-2">描述：</label>
              <div class="col-md-6">
                <textarea type="text"  class="form-control" style="height: 100px;" name="description" v-bind:value="model.description" ></textarea>
              </div>
            </div>
            <div class="form-group">
              <label class="col-md-2">英文描述：</label>
              <div class="col-md-6">
                <textarea type="text"  class="form-control" style="height: 100px;" name="enDescription" v-bind:value="model.enDescription" ></textarea>
              </div>
            </div>

            <div class="form-group">
              <label class="col-md-2">状态：</label>
              <div class="col-md-6">
                <input type="radio"  name="isValid"  value="1"  v-model="model.isValid"/>有效
                <input type="radio"  name="isValid"   value="0" v-model="model.isValid" />无效</div>
            </div>
            <!--发布-->
            <div class="form-group">
              <div class="col-md-3">
                <button class="btn btn-primary" type="submit">保存</button>
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
  import 'jquery-validation'
  import '../../../../static/js/plugins/ueditor/ueditor.config'
  import '../../../../static/js/plugins/ueditor/ueditor.all.min'
  import '../../../../static/js/plugins/uploadUeditor'
  import breadcrumb from "@/components/bootstrap/breadcrumb"

  export default{
    data: function () {
      return {
        resource:commonApp.api.goods.item,
        model: {
          isValid:1,
        },
        categoryList:[],
        colorList:[]
      }
    },
    components: {
      breadcrumb: breadcrumb
    },
    mounted: function () {
      this.submitForm();
      this.upload();
      this.uploadPhotos();
    },
    created: function () {
      var self = this;
      self.upload();
      self.uploadPhotos();
      self.ListColor().then(function () {
        self.get();
      });
    },
    updated: function () {
      this.upload();
      this.uploadPhotos();
    },
    methods: {
     get:function () {
          var self = this;
          return  self.ajax({
            type: "GET",
            url: self.resource.get.url+"?id="+this.$route.params.id,
            success: function (res) {
             self.model = res;
            }
          });
      },
      ListColor:function () {
        var self = this;
        return  self.ajax({
          type: "GET",
          url: commonApp.api.goods.color.list.url,
          success: function (res) {
            self.colorList = res.list;
          }
        });
      },
      submitForm: function () {
        var self = this;
        $('#myForm').validate({
          rules: {
            name: {
              required: true
            },
            description:{
              required: true
            }
          },
          submitHandler: function (form) {
            var $form = $(form);
            self.ajax({
              type: "POST",
              url: self.resource.update.url,
              data: $form.serialize(),
              success: function (res) {
                layer.msg(res.message, {time: 1000}, function () {
                  if (res.code === 200) {
                    self.$router.push('/goods/item/list/'+self.model.goodsId);
                  }
                });
              }
            });
          }
        });
      },
      upload: function () {
        uploadUeditor.singleImage($('#upload_image'));
      },
      uploadPhotos:function () {
        uploadUeditor.multipleImage($('#upload_photos'));
      }
    }
  }
</script>



