<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="edit-wrap container-fluid">
    <div class="page-header mt-0 mb-0">
      <h4>退货单明细</h4>
      <breadcrumb v-bind:items="[{text: '首页', link: '/'},{text: '退货单管理', link: '/returnRequest/list'},{text: '退货单明细', active: true}]"/>
    </div>
    <div class="panel panel-white">
      <div class="panel-body">

          <input type="hidden" name="returnRequestId" v-bind:value="model.returnRequestId"/>
          <div class="form-horizontal">
            <!--标题-->
            <div v-for="(item,index) in model.returnRequestItemDomainList">
              <form class="myForm" method="post" >
              <input type="hidden" name="id" v-bind:value="item.id"/>
              <div class="form-group">
                <hr/>
                <h5 class="col-md-2">退货明细{{index+1}}</h5>
              </div>
            <div class="form-group">
              <label class="col-md-2">退货单编号：</label>
              <div class="col-md-2">
                <input name=""  type="text" readonly="readonly" class="form-control" v-bind:value="item.returnRequestId"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">退货单理由：</label>
              <div class="col-md-6">
                <input name=""  type="text" readonly="readonly" class="form-control" v-bind:value="item.returnReason"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">数量：</label>
              <div class="col-md-1">
                <input name=""  type="text" readonly="readonly" class="form-control" v-bind:value="item.num"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">退货状态：</label>
              <div class="col-md-8" style="padding: 0">
                <div v-if="item.status==1" class="col-md-3">
                  <input   readonly="readonly" type="text" class="form-control input-group-sm status" value="待收货">
                </div>
                <div v-if="item.status==2" class="col-md-3">
                   <input  readonly="readonly" type="text" class="form-control status" value="已收货">
                </div>
                <div v-if="item.status==3" class="col-md-3">
                  <input  readonly="readonly" type="text" class="form-control status" value="已接受退货">
                </div>
                <div v-if="item.status==4" class="col-md-3">
                  <input  readonly="readonly" type="text" class="form-control status" value="已拒绝退货">
                </div>
                <div v-if="item.status==5" class="col-md-3">
                  <input  readonly="readonly" type="text" class="form-control status" value="已取消退货">
                </div>
                <div v-if="item.status==2" class="col-md-10 agreeRadio">
                  <input type="radio"  name="isAgree"  value="1"   />同意退货
                  <input type="radio"  name="isAgree"   value="0"  />不同意退货
                </div>
              </div>
            </div>
            <div class="form-group">
              <label class="col-md-2">管理员备注：</label>
              <div class="col-md-6">
                <input name="adminMemo"  type="text" class="form-control" v-bind:value="item.adminMemo"></div>
                <a   class="btn btn-primary" v-on:click="submitNow($event)" >提交</a>
            </div>

        </form>
            </div>

            <!--发布-->
            <div class="form-group">
              <div class="col-md-3">
                <router-link  v-bind:to="'/returnRequest/list'" class="btn btn-primary">返回</router-link>
              </div>
            </div>
          </div>

      </div>
    </div>
  </div>
</template>

<script>
  import 'jquery-validation'
  import breadcrumb from "@/components/bootstrap/breadcrumb"

  export default{
    data: function () {
      return {
        model: {

        },
      }
    },
    components: {
      breadcrumb: breadcrumb
    },
    mounted: function () {
      this.submitNow();
    },
    created: function () {
      this.getOrder();
    },
    updated: function () {

    },
    methods: {
      getOrder: function () {
        var self = this;
        self.ajax({
          type: "GET",
          url: commonApp.api.returnRequest.get.url+"?id="+this.$route.params.id,
          success: function (res) {
            self.model = res;
          }
        });
      },
      submitNow:function (e) {
        var self = this;
        var now  = e.currentTarget;
        var $form = $(now).parents('.myForm');
        var data = $form.serialize();
        self.ajax({
              type: "POST",
              url: commonApp.api.returnRequest.isAgree.url,
              data:data,
              success: function (res) {
                layer.msg(res.message, {time: 1000}, function () {
                  if (res.code === 200) {
                    var str = $form.serializeArray()[1].value==1&&$form.serializeArray()[1].value!=null?"已同意退货":"已拒绝退货";
                    $form.find(".status").val(str);
                    $form.find(".agreeRadio").remove();
                  }
                });
              }
            });
      }

    }
  }
</script>



