<template xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="edit-wrap container-fluid">
    <div class="page-header mt-0 mb-0">
      <h4>sku创建</h4>
      <breadcrumb v-bind:items="[{text: '首页', link: '/'},{text: '角色管理', link: '/index/role/list'},{text: '角色编辑', active: true}]"/>
    </div>
    <div class="panel panel-white">
      <div class="panel-body">
        <form id="myForm" method="post">
          <input type="hidden" name="id" v-bind:value="model.id"/>
          <input type="hidden" name="goodsId" v-bind:value="model.goodsId"/>
          <input type="hidden" name="goodsNo" v-bind:value="model.goodsNo"/>
          <input type="hidden" name="itemId" v-bind:value="model.itemId"/>
          <div class="form-horizontal">

            <div class="form-group">
              <label class="col-md-2">尺寸：</label>
              <div class="col-md-6">
                <select name="categoryId" class="form-control" v-model="specifications.size">
                  <option v-for="item in sizeList" v-bind:value="item.id">{{item.name}}</option>

                </select>
              </div>
            </div>
            <input name="specifications" type="hidden"  v-bind:value="specificationsJson"/>
            <div class="form-group">
              <label class="col-md-2">库存数量：</label>
              <div class="col-md-6">
                <input name="quantity"  type="number" class="form-control" v-bind:value="model.quantity" v-model="model.quantity"></div>
            </div>

            <div class="form-group">
              <label class="col-md-2">状态：</label>
              <div class="col-md-6">
                <input type="radio"  name="isValid"  value="1"  v-model="model.isValid"/>有效
                <input type="radio"  name="isValid"   value="0" v-model="model.isValid" />无效</div>
            </div>
            <!--发布-->
            <div class="form-group">
              <div class="col-md-3">
                <button class="btn btn-primary" type="submit">保存</button>
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
  import 'jquery-validation'

  import breadcrumb from "@/components/bootstrap/breadcrumb"

  export default{
    data: function () {
      return {
        resource:commonApp.api.goods.sku,
        model: {
          isValid:1,
          itemId:this.$route.params.id
        },
        specifications:{},
        specificationsJson:"",
        categoryList:[],
        goodsItem:{},
        goods:{},
        sizeList:[],

      }
    },
    components: {
      breadcrumb: breadcrumb
    },
    mounted: function () {
      this.submitForm();

    },
    created: function () {
      var self = this;

      this.getGoodsItem().then(function () {
        self.getGoods();
      });

    },
    updated: function () {
      this.specificationsJson = JSON.stringify(this.specifications);
    },
    methods: {
      getGoodsItem:function(){
        var self = this;
        var query = "?id="+this.$route.params.id;
        return  self.ajax({
          type: "GET",
          url: commonApp.api.goods.item.get.url+query,
          success: function (res) {
            self.goodsItem = res;
          }
        });
      },
      getGoods:function(){
        var self = this;
        var query = "?id="+ self.goodsItem.goodsId+"&withSpecificationList=1"
        return  self.ajax({
          type: "GET",
          url: commonApp.api.goods.get.url+query,
          success: function (res) {
            self.model.goodsNo = res.code;
            self.model.goodsId = res.id;
            self.goods = res;
            self.sizeList = self.goods.specificationList[0].prototypeSpecificationOptionList;
          }
        });
      },

      submitForm: function () {
        var self = this;
        $('#myForm').validate({
          rules: {
            name: {
              required: true
            },
            description:{
              required: true
            }
          },
          submitHandler: function (form) {
            var $form = $(form);
            self.ajax({
              type: "POST",
              url: self.resource.create.url,
              data: $form.serialize(),
              success: function (res) {
                layer.msg(res.message, {time: 1000}, function () {
                  if (res.code === 200) {
                    self.$router.push('/goods/sku/list/'+self.$route.params.id);
                  }
                });
              }
            });
          }
        });
      },
      upload: function () {
        uploadUeditor.singleImage($('#upload_image'));
      }
    }
  }
</script>



