<template>
  <div class="dk-login-wrapper">
    <div class="dk-login-panel">
      <div class="panel">
        <div class="panel-heading">
          <img src="../../assets/images/LOGO-1.png">
          <h1>东东TV运营后台</h1>
        </div>
        <div class="panel-body">
          <form id="js-login-form" method="post">
            <div class="form-group">
              <div class="input-group">
                <span class="input-group-addon"><span class="ion ion-ios-person-outline"></span></span>
                <input type="text" class="form-control" placeholder="用户名" name="userName">
              </div>
            </div>
            <div class="form-group">
              <div class="input-group">
                <span class="input-group-addon"><span class="ion ion-ios-unlocked-outline"></span> </span>
                <input type="password" class="form-control" placeholder="密码" name="password">
              </div>
            </div>
            <!--<div class="form-group">-->
            <!--<div class="input-group">-->
            <!--<span class="input-group-addon"><span class="ion ion-ios-nutrition-outline"></span></span>-->
            <!--<input type="text" class="form-control" placeholder="验证码">-->
            <!--<span class="input-group-addon dk-verification-code">-->
            <!--<img src="data:image/png;base64,">-->
            <!--</span>-->
            <!--</div>-->
            <!--</div>-->
            <div class="checkbox">
             <!-- <label>
                <input type="checkbox"> 记住账号
              </label>-->
            </div>
            <button type="submit" class="btn btn-brand btn-block">登录</button>
          </form>
        </div>
      </div>
    </div>

    <p class="dk-copyright">©2017 稻壳互联 沪ICP备12018056号-1 <a href="#" target="_blank">www.dookay.com</a></p>
    <div id="js-particles" class="dk-particles"></div>
  </div>
</template>

<script>
  import appriseComment from "../../../static/js/plugins/particles/particles.min"

  export default{
    mounted: function () {
      var self = this;
      particlesJS.load('js-particles', '/static/js/plugins/particles/particles.json');
      self.submitForm();
    },
    methods: {
      submitForm: function(){
        var self = this;
        $('#js-login-form').validate({
          rules: {
            name: {
              required: true
            },
            level1Per: {
              required: true
            },
            level2Per: {
              required: true
            },
            description: {
              required: true
            }
          },
          submitHandler: function (form) {
            var $form = $(form);
            $.ajax({
              type: "POST",
              url: commonApp.api.login,
              data: $form.serialize(),
              success: function (res) {
                layer.msg(res.message, {time: 1000}, function () {
                  if (res.code === 200) {
                    window.sessionStorage.userContext=JSON.stringify(res);
                    self.$router.push('/index/info');
                  }
                });
              }
            })
          }
        });
      }
    }
  }

</script>



