<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="edit-wrap container-fluid">
    <div class="page-header mt-0 mb-0">
      <h4>门店门店修改</h4>
      <breadcrumb v-bind:items="[{text: '首页', link: '/'},{text: '门店门店管理', link: '/store/city/list'},{text: '门店门店修改', active: true}]"/>
    </div>
    <div class="panel panel-white">
      <div class="panel-body">
        <form id="myForm" method="post">
          <input type="hidden" name="id" v-model="model.id"/>
          <div class="form-horizontal">
            <!--标题-->
            <div class="form-group">
              <label class="col-md-2">名称：</label>
              <div class="col-md-6">
                <input name="name"  type="text" class="form-control" v-model="model.name">
              </div>
            </div>
            <div class="form-group">
              <label class="col-md-2">英文名称：</label>
              <div class="col-md-6">
                <input name="enName"  type="text" class="form-control" v-model="model.enName">
              </div>
            </div>
            <div class="form-group">
              <label class="col-md-2">国家：</label>
              <div class="col-md-6">
                <select name="countryId" class="form-control countryId" v-model="model.countryId">
                  <option v-for="item in countryList.list" v-bind:value="item.id" v-if="item.id==model.countryId" selected>{{item.name}}</option>
                  <option v-for="item in countryList.list" v-bind:value="item.id" v-if="item.id!=model.countryId">{{item.name}}</option>
                </select>
              </div>
            </div>
            <div class="form-group">
              <label class="col-md-2">是否有效：</label>
              <div class="col-md-6">
                <input type="radio"  name="isValid"  value="1"  v-model="model.isValid"/>是
                <input type="radio"  name="isValid"   value="0" v-model="model.isValid" />否
              </div>
            </div>
            <div class="form-group">
              <label class="col-md-2">排序：</label>
              <div class="col-md-6">
                <input name="rank"  type="number" class="form-control" v-model="model.rank">
              </div>
            </div>
            <!--发布-->
            <div class="form-group">
              <div class="col-md-3">
                <button class="btn btn-primary" type="submit">保存</button>
              </div>
            </div>

          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
  import 'jquery-validation'
  import breadcrumb from "@/components/bootstrap/breadcrumb"

  export default{
    data: function () {
      return {
        resource:commonApp.api.store.city,
        model: {

        },
        countryList:[]
      }
    },
    components: {
      breadcrumb: breadcrumb
    },
    mounted: function () {
      this.submitForm();
    },
    created: function () {
      this.get();
      this.ListCountry();
    },
    updated: function () {

    },
    methods: {
      get: function () {
        var self = this;
        return  self.ajax({
          type: "GET",
          url: self.resource.get.url+"?id="+this.$route.params.id,
          success: function (res) {
            if(res){
              self.model = res;
            }
          }
        });
      },
      ListCountry:function () {
        var self = this;
        return  self.ajax({
          type: "GET",
          url: commonApp.api.store.country.list.url,
          success: function (res) {
            self.countryList = res;
          }
        });
      },
      submitForm: function () {
        var self = this;
        $('#myForm').validate({
          rules: {
            name: {
              required: true
            },
            countryId:{
              required: true
            },
            cityId:{
              required: true
            }
          },
          submitHandler: function (form) {
            var $form = $(form);
            self.ajax({
              type: "POST",
              url: self.resource.update.url,
              data: $form.serialize(),
              success: function (res) {
                layer.msg(res.message, {time: 1000}, function () {
                  if (res.code === 200) {
                    self.$router.push('/store/city/list');
                  }
                });
              }
            });
          }
        });
      }
    }
  }
</script>



