<template>
  <transition
    enter-active-class="animated slideInUp"
    leave-active-class="animated slideOutUp">
    <router-view></router-view>
  </transition>
</template>

<script>
  export default {
    name: 'dk-app'
  }
</script>

<style>
  .animated.dk-app-wrapper,
  .animated.dk-login-wrapper{
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
  }
</style>



