<template>
  <div class="container-fluid">
    <div class="page-header mb-0 mt-0">
      <div class="pull-left">
        <h4>概况</h4>
        <ol class="breadcrumb">
          <li><a href="#">首页</a></li>
          <li class="active">概况</li>
        </ol>
      </div>
    </div>

    <div class="panel panel-white">
      <div class="panel-heading">
        <h5><span class="text-brand">▌ </span>今日实时
          <small>（更新时间：“当前页面刷新时间”）</small>
        </h5>
      </div>
      <div class="panel-body">
        <div class="row widget-box-inline">
          <div class="widget-box-item text-center col-lg-3 col-sm-6">
            <h3><span class="text-primary ion ion-android-cart"></span><span
              class="widget-box-counter">{{today.viewPv}}</span></h3>
            <h4 class="text-muted">页面访问次数</h4>
          </div>

          <div class="widget-box-item text-center col-lg-3 col-sm-6">
            <h3><i class="text-success ion ion-social-usd"></i><span class="widget-box-counter">{{today.viewUv}}</span>
            </h3>
            <h4 class="text-muted">页面访问人数</h4>
          </div>

          <div class="widget-box-item text-center col-lg-3 col-sm-6">
            <h3><i class="text-warning ion ion-android-people"></i><span
              class="widget-box-counter">{{today.sharePv}}</span></h3>
            <h4 class="text-muted">分享访问次数</h4>
          </div>

          <div class="widget-box-item text-center col-lg-3 col-sm-6">
            <h3><i class="text-info ion ion-eye"></i><span class="widget-box-counter">{{today.shareUv}}</span></h3>
            <h4 class="text-muted">分享访问人数</h4>
          </div>

        </div>
      </div>
    </div>

    <div class="panel panel-white">
      <div class="panel-heading">
        <h5><span class="text-brand">▌ </span>昨日经营状况</h5>
      </div>
      <div class="panel-body">
        <div class="row widget-box-inline">
          <div class="widget-box-item text-center col-lg-3 col-sm-6">
            <h3><span class="text-primary ion ion-android-cart"></span><span
              class="widget-box-counter">{{yesterday.viewPv}}</span></h3>
            <h4 class="text-muted">页面访问次数</h4>
          </div>

          <div class="widget-box-item text-center col-lg-3 col-sm-6">
            <h3><i class="text-success ion ion-social-usd"></i><span
              class="widget-box-counter">{{yesterday.viewUv}}</span>
            </h3>
            <h4 class="text-muted">页面访问人数</h4>
          </div>

          <div class="widget-box-item text-center col-lg-3 col-sm-6">
            <h3><i class="text-warning ion ion-android-people"></i><span
              class="widget-box-counter">{{yesterday.sharePv}}</span></h3>
            <h4 class="text-muted">分享访问次数</h4>
          </div>

          <div class="widget-box-item text-center col-lg-3 col-sm-6">
            <h3><i class="text-info ion ion-eye"></i><span class="widget-box-counter">{{yesterday.shareUv}}</span></h3>
            <h4 class="text-muted">分享访问人数</h4>
          </div>

        </div>
      </div>
    </div>

    <div class="panel panel-white">
      <div class="panel-heading">
        <h5><span class="text-brand">▌ </span>折线图
          <small>（对比）</small>
        </h5>
      </div>
      <div class="panel-body">
        <div id="js-chart-line" style="height: 700px;"></div>
      </div>
    </div>

  </div>
</template>

<script>
  import echarts from 'echarts'

  export default {
    data: function () {
      return {
        today: {
          viewPv: 0,
          viewUv: 0,
          sharePv: 0,
          shareUv: 0
        },
        yesterday: {
          viewPv: 0,
          viewUv: 0,
          sharePv: 0,
          shareUv: 0
        }
      }
    },
    created: function () {
      this.getToday();
      this.getYesterday();
      this.mounted();
    },
    methods: {
      getToday: function () {
        var self = this;
        self.get(commonApp.api.stat.getToday.url, function (res) {
          self.today = res;
        });
      },
      getYesterday: function () {
        var self = this;
        self.get(commonApp.api.stat.getYesterday.url, function (res) {
          self.yesterday = res;
        });
      },
    mounted: function () {
      var self = this;
      self.get(commonApp.api.stat.getLine.url, function (result) {
        let chartBox_line = document.getElementById("js-chart-line");
        var myChart_line = echarts.init(chartBox_line);
        var option_line = {
          title: {
            text: '访问统计'
          },
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            data: ['页面访问次数', '页面访问人数', '分享访问次数', '分享访问人数']
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: result.dateList
          },
          yAxis: {
            type: 'value'
          },
          series: [
            {
              name: '页面访问次数',
              type: 'line',
              stack: '总量',
              data: result.viewPvList
            },
            {
              name: '页面访问人数',
              type: 'line',
              stack: '总量',
              data: result.viewUvList
            },
            {
              name: '分享访问次数',
              type: 'line',
              stack: '总量',
              data: result.sharePvList
            },
            {
              name: '分享访问人数',
              type: 'line',
              stack: '总量',
              data: result.shareUvList
            }
          ]
        };
        myChart_line.setOption(option_line);
      });
    }
    },
  }
</script>



