<template xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="edit-wrap container-fluid">
    <div class="page-header mt-0 mb-0">
      <h4>预订单创建</h4>
      <breadcrumb v-bind:items="[{text: '首页', link: '/'},{text: '预订单管理', link: '/reservation/list'},{text: '预订单编辑', active: true}]"/>
    </div>
    <div class="panel panel-white">
      <div class="panel-body">
        <form id="myForm" method="post">
          <input type="hidden" name="id" v-bind:value="model.id"/>
          <div class="form-horizontal">
            <!--标题-->
            <div class="form-group">
              <label class="col-md-2">名称：</label>
              <div class="col-md-6">
                <input name="name"  type="text" class="form-control" v-bind:value="model.name"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">原型编号：</label>
              <div class="col-md-6">
                <input name="code"  type="text" class="form-control" v-bind:value="model.code"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">分类：</label>
              <div class="col-md-6">
                <select name="categoryId" class="form-control" v-model="model.categoryId">
                  <option v-for="item in roleList" v-bind:value="item.id">{{item.name}}</option>
                </select>
              </div>
            </div>
            <div class="form-group">
              <label class="col-md-2">描述：</label>
              <div class="col-md-6">
                <textarea type="text"  class="form-control" name="description" v-bind:value="prototype.description" ></textarea>
              </div>
            </div>
            <div class="form-group">
              <label class="col-md-2">状态：</label>
              <div class="col-md-6">
                <input type="radio"  name="isPublished"  value="1"  v-model="model.isPublished"/>上架
                <input type="radio"  name="isPublished"   value="0" v-model="model.isPublished" />下架</div>
            </div>

            <!--发布-->
            <div class="form-group">
              <div class="col-md-3">
                <button class="btn btn-primary" type="submit">保存</button>
              </div>
            </div>

          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
  import 'jquery-validation'
  import breadcrumb from "@/components/bootstrap/breadcrumb"

  export default{
    data: function () {
      return {
        resource:commonApp.api.reservation,
        model: {

        },
      }
    },
    components: {
      breadcrumb: breadcrumb
    },
    mounted: function () {
      this.submitForm();
    },
    created: function () {

    },
    updated: function () {

    },
    methods: {

      submitForm: function () {
        var self = this;
        $('#myForm').validate({
          rules: {
            name: {
              required: true
            },
            description:{
              required: true
            }
          },
          submitHandler: function (form) {
            var $form = $(form);
            self.ajax({
              type: "POST",
              url: self.resource.update.url,
              data: $form.serialize(),
              success: function (res) {
                layer.msg(res.message, {time: 1000}, function () {
                  if (res.code === 200) {
                    self.$router.push('/goods/prototype/list');
                  }
                });
              }
            });
          }
        });
      }
    }
  }
</script>



