<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="store-list container-fluid">
    <div class="page-header mb-0 mt-0">
      <h4>退货单列表</h4>
      <breadcrumb
        v-bind:items="[{text: '首页', link: '/'},{text: '退货单管理', link: 'index/  role/list'},{text: '退货单列表', active: true}]"/>
    </div>
    <div class="panel panel-white">
      <div class="panel-body">
        <form id="search-form" class="j_search" method="get">
          <div class="form-inline form-group">
            <div class="form-group">
              <input type="text" class="form-control" name="orderNo" placeholder="关键词" v-bind:value="form.orderNo"/>
            </div>
            <button class="btn btn-primary" type="submit" >查询</button>
            <a class="btn btn-default j_reset" >重置</a>
          </div>
        </form>
        <table class="table table-bordered table-hover">
          <thead>
          <tr>
            <th>序号</th>
            <th>退货单编号</th>
            <th>下单时间</th>
            <th>申请时间</th>
            <th>退货方式</th>
            <th>收货人姓名</th>
            <th>收货人地址</th>
            <th>备注</th>
            <th>退货人</th>
            <th>操作</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="item in dataList">
            <td>{{item.id}}</td>
            <td>{{item.orderNo}}</td>
            <td>{{item.orderTime | formatDate}}</td>
            <td>{{item.createTime | formatDate}}</td>
            <td>{{item.returnShippingMethod==1?"快递取货":"退回门店"}}</td>
            <td>{{item.shipName}}</td>
            <td>{{item.shipAddress}}</td>
            <td>{{item.memo}}</td>
            <td>{{item.customerDomain.lastName+item.customerDomain.firstName}}</td>
            <td>
              <router-link v-bind:to="'/returnRequest/get/'+ item.id" class="btn btn-primary">查看详情</router-link>
            </td>
          </tr>
          </tbody>
        </table>

        <pagination size="md"
                    :total-rows="pagination.totalRows"
                    :per-page="pagination.perPage"
                    v-model="currentPage">
        </pagination>

      </div> <!--panel-body end-->
    </div><!--panel end-->
  </div>
</template>


<script>
  import 'jquery-validation'
  import pagination from "@/components/bootstrap/pagination"
  import breadcrumb from "@/components/bootstrap/breadcrumb"

  export default{
    data: function () {
      return {
        resource:commonApp.api.returnRequest,
        dataList: [],
        pagination: {
          totalRows: 0,
          perPage: 1
        },
        currentPage: 1,
        form: {
          orderNo: ''
        }
      }
    },
    components: {
      pagination: pagination,
      breadcrumb: breadcrumb
    },
    mounted: function () {
      this.search();
      this.reset();
    },
    created: function () {
      this.list();
    },
    update:function () {

    },
    methods: {
      list: function () {
        var self = this;
        self.ajax({
          url: self.resource.list.url,
          data: self.form,
          success: function (res) {
            self.dataList = res.list;
            self.pagination.totalRows = res.totalRecord;
            self.pagination.perPage = res.pageSize;
            self.currentPage = res.pageIndex;
          }
        });
      },
      search: function () {
        var self = this;
        $('.j_search').submit(function (e) {
          e.preventDefault();
          self.form = commonApp.tools.getFromData($(this));
          self.list();
        });
      },
      reset: function () {
        var self = this;
        $('.j_reset').click(function (e) {
          e.preventDefault();
          self.form = {
            orderNo: ''
          };
        });
      }
  },
    watch: {
      currentPage: function (newData) {
        var self = this;
        if (self.$route.query.page != newData) {
          self.form.pageIndex = newData;
          self.list();
        }
      }
    }
  }
</script>



