<template>
  <div id="dk-app" class="dk-app-wrapper">
    <!-- 顶部导航 -->
    <nav class="navbar navbar-inverse dk-navbar">
      <div class="navbar-header">
        <button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#dk-navbar-collapse" aria-expanded="false">
          <span class="sr-only">切换导航</span>
          <span class="icon-bar"></span>
          <span class="icon-bar"></span>
          <span class="icon-bar"></span>
        </button>
        <a class="navbar-brand" href="#">
          <img src="../assets/images/LOGO-1.png" >
          <span>夏姿陈运营后台</span>
        </a>
      </div>
      <div class="collapse navbar-collapse" id="dk-navbar-collapse">
        <!-- 顶部其他导航 -->
        <ul class="nav navbar-nav navbar-right">
          <li class="hidden-xs">
            <a id="js-full-screen" href="javascript:;">
              <span class="ion ion-arrow-expand"></span>
            </a>
          </li>
          <li class="dropdown dk-user-control">
            <a href="#" class="dropdown-toggle" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">
              <img class="dk-avatar" src="../assets/images/avatar.png">
              <span></span>
              <span class="caret"></span>
            </a>
            <ul class="dropdown-menu">
              <li><a class="" v-on:click.prevent="logout" href="#"><span class="ion ion-log-out"></span>退出</a>
                <router-link to='/index/account/change-password'><span class="ion ion-compose"></span><span>修改密码</span>
                </router-link>
              </li>
            </ul>
          </li>
        </ul>
      </div>
    </nav><!-- 顶部导航 结束 -->

    <!-- 侧边导航 -->
    <aside class="dk-main-aside">
      <a id="js-toggle-aside-nav" class="dk-nav-shrink-btn" href="javascript:;"><span class="ion ion-navicon-round"></span></a>
      <nav class="dk-nav-aside scroll-bar">
        <ul id="js-aside-collapse">
          <li>
            <router-link to="/index/info"><span class="ion ion-ios-calendar"></span><span>平台概况</span></router-link>
          </li>
          <li>
            <a href="#"><span class="ion ion-ios-calendar"></span><span>商品管理</span></a>
            <ul>
              <li><router-link to="/goods/list"><span>商品列表</span></router-link></li>
              <li><router-link to="/goods/category/list"><span>分类管理</span></router-link></li>
              <li><router-link to="/goods/prototype/list"><span>原型管理</span></router-link></li>
            </ul>
          </li>
          <li>
            <a href="#" ><span class="ion ion-ios-calendar"></span><span>订单管理</span></a>
            <ul>
              <li><router-link to="/order/list"><span>订单列表</span></router-link></li>
              <li><router-link to="/returnRequest/list"><span>退货列表</span></router-link></li>
              <li><router-link to="/reservation/list"><span>预订列表</span></router-link></li>
            </ul>
          </li>
          <li>
            <a href="#" ><span class="ion ion-pie-graph"></span><span>会员管理</span></a>
            <ul>
              <li><router-link to="/customer/list"><span>会员列表</span></router-link></li>
            </ul>
          </li>
          <li>
            <a href="#" ><span class="ion ion-pie-graph"></span><span>门店管理</span></a>
            <ul>
              <li><router-link to="/store/list"><span>门店列表</span></router-link></li>
              <li><router-link to="/store/country/list"><span>国家管理</span></router-link></li>
              <li><router-link to="/store/city/list"><span>城市管理</span></router-link></li>
            </ul>
          </li>
          <li>
            <a href="#" ><span class="ion ion-ios-calendar"></span><span>安全管理</span></a>
            <ul>
              <li><router-link to="/customer/account/list"><span>角色管理</span></router-link></li>
              <li><router-link to="/index/role/list"><span>用户管理</span></router-link></li>
            </ul>
          </li>
        </ul>
      </nav>
    </aside><!-- 侧边导航 结束 -->

    <!-- 主内容 -->
    <div class="dk-main-container">
      <transition
        enter-active-class="animated fadeIn"
        leave-active-class="animated fadeOut">
        <router-view></router-view>
      </transition>
    </div>
  </div>
</template>

<script>
  export default {
    created: function () {
      var self = this;
      if (window.sessionStorage.userContext==undefined) {
        //self.$router.push('/passport/login');
      }
    },
    mounted: function () {
      commonApp.init();
    },
    methods: {
      logout: function () {
        window.sessionStorage.clear();
        this.$router.push('/passport/login');
      },
      isEmptyObject: function (obj) {
        for (var key in obj) {
          return false;
        }
        return true;
      },
      contains: function (str) {
        if(str=='')return false;
        var user=JSON.parse(window.sessionStorage.userContext);
        for (var i in user.permissions) {
          var list=str.split(',');
          for(var j in list) {
            if (user.permissions[i] == list[j]) return true;
          }
        }
        return false;
      }
    },
    computed: {
      user(){
        return JSON.parse(window.sessionStorage.userContext);
      }
    }
  }
</script>

<style>
  .dk-app-wrapper{height: 100%;}

  .dk-main-container>.animated{
    position: absolute;
    left: 10px;
    right: 10px;
  }
</style>



