<template xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="edit-wrap container-fluid">
    <div class="page-header mt-0 mb-0">
      <h4>分类创建</h4>
      <breadcrumb v-bind:items="[{text: '首页', link: '/'},{text: '角色管理', link: '/index/role/list'},{text: '角色编辑', active: true}]"/>
    </div>
    <div class="panel panel-white">
      <div class="panel-body">
        <form id="myForm" method="post">
          <input type="hidden" name="id" v-bind:value="model.id"/>
          <input type="hidden" name="parentId" v-bind:value="model.parentId"/>
          <input type="hidden" name="level" v-bind:value="model.level"/>
          <div class="form-horizontal">
            <!--标题-->
            <div class="form-group">
              <label class="col-md-2">分类名称：</label>
              <div class="col-md-6">
                <input name="name"  type="text" class="form-control" v-bind:value="model.name"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">分类编号：</label>
              <div class="col-md-6">
                <input name="slug"  type="text" class="form-control" v-bind:value="model.slug"></div>
            </div>
            <div class="form-group">
              <label class="col-md-2">原型：</label>
              <div class="col-md-6">
                <select name="prototypeId" class="form-control" v-model="model.prototypeId">
                  <option v-for="item in prototypeList" v-bind:value="item.id">{{item.name}}</option>
                </select>
              </div>
            </div>
            <div class="form-group">
              <label class="col-md-2">图片</label>
              <div class="col-md-6">
                <div class="list-img list-img-multiple" id="upload_image">
                  <input class="upload_input" name="thumb" type="hidden" v-bind:value="model.thumb"/>
                  <ul class="upload_list"></ul>
                  <a class="upload upload_btn" href="javascript:;"><span class="ion ion-plus"></span></a>
                </div>
              </div>
            </div>
            <div class="form-group">
              <label class="col-md-2">描述：</label>
              <div class="col-md-6">
                <textarea type="text"  class="form-control" name="description" v-bind:value="model.description" ></textarea>
              </div>
            </div>
            <div class="form-group">
              <label class="col-md-2">状态：</label>
              <div class="col-md-6">
                <input type="radio"  name="isValid"  value="1"  v-model="model.isValid"/>有效
                <input type="radio"  name="isValid"   value="0" v-model="model.isValid" />无效</div>
            </div>

            <!--发布-->
            <div class="form-group">
              <div class="col-md-3">
                <button class="btn btn-primary" type="submit">保存</button>
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
  import 'jquery-validation'
  import '../../../../static/js/plugins/ueditor/ueditor.config'
  import '../../../../static/js/plugins/ueditor/ueditor.all.min'
  import '../../../../static/js/plugins/uploadUeditor'
  import breadcrumb from "@/components/bootstrap/breadcrumb"

  export default{
    data: function () {
      return {
        resource:commonApp.api.goods.category,
        model: {
          isValid:1,
          parentId:this.$route.query.parentId,
          level:1
        },
        prototypeList:[],

      }
    },
    components: {
      breadcrumb: breadcrumb
    },
    mounted: function () {
      this.submitForm();
      this.upload();
    },
    created: function () {
      this.ListPrototype();
      this.check();
      this.upload();
    },
    updated: function () {

    },
    methods: {
      check:function () {
        var self = this;
        if(self.model.parentId){
          self.model.level = 2;
        }else{
          self.model.parentId = 0;
        }
      },
      ListPrototype:function () {
        var self = this;
        self.get(commonApp.api.prototype.list.url, function (res) {
          self.prototypeList = res;
        });
      },
      submitForm: function () {
        var self = this;
        $('#myForm').validate({
          rules: {
            name: {
              required: true
            },
            description:{
              required: true
            }
          },
          submitHandler: function (form) {
            var $form = $(form);
            self.ajax({
              type: "POST",
              url: self.resource.create.url,
              data: $form.serialize(),
              success: function (res) {
                layer.msg(res.message, {time: 1000}, function () {
                  if (res.code === 200) {
                    self.$router.push('/goods/category/list');
                  }
                });
              }
            });
          }
        });
      },
      upload: function () {
        uploadUeditor.singleImage($('#upload_image'));
      }
    }
  }
</script>



